import React, { Component } from "react";
import "../assets/css/elacorte.css";
import "../assets/css/animate.css";
import "../assets/js/functions.js";

import Main from "../components/Main.jsx";
import Design from "../components/Design.jsx";
import Dev from "../components/Dev.jsx";
import Footer from "../components/Footer.jsx";
import menu from "../assets/images/iconfinder-menu.svg";
import logo from "../assets/images/logo-white.svg";

import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

class App extends Component {
  render() {
    return (
      <div className="App">
        <Router>
          <Link to={"/"}>
            <img src={logo} className="sub-logo" alt="elacorte" />
          </Link>
          <div>
            <img src={menu} alt="menu" className="menu" />
            <nav>
              <ul className="navbar-nav mr-auto">
                <li>
                  <Link to={"/"} className="nav-link nav-link-home">
                    Home
                  </Link>
                </li>
                <li>
                  <Link to={"/design"} className="nav-link">
                    Design
                  </Link>
                </li>
                <li>
                  <Link to={"/development"} className="nav-link">
                    Development
                  </Link>
                </li>
              </ul>
            </nav>

            <Switch>
              <Route exact path="/" component={Main} />
              <Route path="/design" component={Design} />
              <Route path="/development" component={Dev} />
              <Route path="/works" component={Footer} />
            </Switch>
          </div>
        </Router>
      </div>
    );
  }
}

export default App;
