import $ from 'jquery';

$(document).ready(function () {
    $('.menu').next().hide();
    $('.sub-logo').hide();
    $('.menu').click(function () {
        $(this).next().slideToggle();
    });
    $('.nav-link').click(function () {
        $('.menu').next().hide();
        if ($(this).hasClass('nav-link-home')) {
            $('.sub-logo').hide();
        } else {
            $('.sub-logo').show();
        }
    });
    $('.sub-logo').click(function () {
        $(this).hide();
    });
})